.banner{position:relative;color:#fff;padding:150px 15px 100px;text-align:center}@media(max-width: 767.98px){.banner{padding:120px 15px 100px}}.banner__img{position:absolute;top:0;left:0;width:100%;height:100%;z-index:-1;object-fit:cover}.banner__title{font-weight:700;font-size:40px;line-height:58px;text-shadow:0px 4px 4px rgba(0,0,0,.25)}@media(max-width: 479.98px){.banner__title{font-size:30px}}.banner__icon{display:flex;justify-content:center;align-items:center;column-gap:25px;margin:20px 0px 34px 0px}.banner__icon::after{content:"";width:60px;height:1px;background-color:#fff;box-shadow:0px 2px 4px rgba(0,0,0,.25)}.banner__icon::before{content:"";width:60px;height:1px;background-color:#fff;box-shadow:0px 2px 4px rgba(0,0,0,.25)}.banner__info{margin:0px auto 18px;max-width:530px;font-weight:700;font-size:24px;line-height:1.8}@media(max-width: 479.98px){.banner__info{font-size:18px}}.banner__btn{width:120px;height:30px;border:1px solid #fff;filter:drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));border-radius:3px;font-family:inherit;font-weight:700;font-size:14px;line-height:20px}.banner ._icon-logo_2:before{color:#fff;content:"";font-size:29px;filter:drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25))}