.decor {
	display: flex;
	justify-content: center;
	align-items: center;
	column-gap: 25px;
	margin: 0px 0px 40px 0px;
	&::after {
		content: "";
		width: 60px;
		height: 1px;
		background-color: #000;
	}
	&::before {
		content: "";
		width: 60px;
		height: 1px;
		background-color: #000;
	}
}
